import React, { useState } from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";
import { useHistory, Link } from "react-router-dom";

import { withStyles } from "@mui/styles";
import { FaLock } from "react-icons/fa";
import { Paper, Box, TextField, InputAdornment, Divider } from "@mui/material";

import stylesForm from "./stylesForm";
import LogoImg from "../../Pages/Assets/brandmark-design.png";

const VerifyCodeForm = ({ classes }) => {
  const history = useHistory();
  const [verifyCode, setVerifyCode] = useState();

  const sendEmailFunc = () => {
    if (!verifyCode) toast.error("Fgen Sie den VerifyCode ein");
    else {
      toast.success("Wir senden das genderte Passwort an Ihre E-Mail.");
      history.push("/login");
    }
  };

  return (
    <Paper elevation={6} className={classes.paper} style={{ marginTop: "45px" }}>
      <Box display="flex" justifyContent="center" className={classes.loginHeader}>
        <img src={LogoImg} alt="logo_img" style={{ width: "50%" }} />
      </Box>

      <TextField
        name="verifycode"
        label="VerifyCode"
        type="number"
        variant="outlined"
        margin="normal"
        value={verifyCode}
        onChange={(e) => setVerifyCode(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <FaLock />
            </InputAdornment>
          ),
        }}
      />

      <div id="authButton" className="buttonContainer" onClick={sendEmailFunc}>
        <span>Code berprfen</span>
      </div>
      <Box style={{ marginTop: 10 }}>
        <Divider />
        <Box className={classes.helpText}>
          Sie haben kein Konto?
          <Link to="/login">Sign In</Link>
        </Box>
      </Box>
    </Paper>
  );
};

VerifyCodeForm.propTypes = {
  classes: PropTypes.object.isRequired,
  type1: PropTypes.string.isRequired,
};

export default withStyles(stylesForm)(VerifyCodeForm);
