import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";
import { toast } from "react-toastify";
import { useHistory, Link } from "react-router-dom";

import { withStyles } from "@mui/styles";
import { FaUserAlt, FaLock, FaRegEnvelope, FaPhone } from "react-icons/fa";
import { Paper, Box, Divider, TextField, InputAdornment } from "@mui/material";
import { Visibility as IconVisiblility, VisibilityOff as IconVisibilityOff } from "@mui/icons-material";

import auth from "../../auth";
import stylesForm from "./stylesForm";
import { USER_LOGIN } from "../../store/type";
import AuthButton from "../AuthButton/AuthButton";
import { userRegFunc, userLoginFunc } from "../../actions/user";
import LogoImg from "../../Pages/Assets/brandmark-design.png";

const Form = ({ classes, type1 }) => {
  const dispatch = useDispatch();
  const [visibility, setVisibility] = React.useState(false);
  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [phone, setPhone] = useState();
  const [password, setPassword] = useState();
  const [confirm, setConfirm] = useState();
  const [formType, setformType] = useState("");
  const isLoginForm = formType === "login" ? true : false;

  let history = useHistory();

  useEffect(() => {
    if (type1 === "login") setformType("login");
  }, []);

  const changeFormType = () => {
    formType === "login" ? setformType("signup") : setformType("login");
  };

  const handleSignIn = () => {
    const loginUserInfo = {
      email: email,
      password: password,
    };
    userLoginFunc(loginUserInfo)
      .then((res) => {
        if (res.status === 201) {
          auth.login(() => {
            auth.sub = res.data._id;
            auth.email = res.data.email;
            auth.phone = res.data.phone;
            auth.name = res.data.fullName;
            auth.token = res.data.access_token;
            dispatch({ type: USER_LOGIN, payload: res.data });
            history.push("/dashboard");
          });
        } else {
          console.log(res);
          // toast.error("Email or Password Error!");
        }
      })
      .catch((err) => {
        toast.error(err.response.data);
      });
  };

  const handleSignUp = () => {
    if (confirm === password && !(confirm === "" && password === "" && name === "" && email === "")) {
      const userInfos = {
        name: name,
        email: email,
        phone: phone,
        password: password,
      };
      userRegFunc(userInfos)
        .then((res) => {
          if (res.status === 201) {
            auth.login(() => {
              auth.sub = res.data._id;
              auth.phone = res.data.phone;
              auth.email = res.data.email;
              auth.name = res.data.fullName;
              auth.token = res.data.access_token;
              dispatch({ type: USER_LOGIN, payload: res.data });
              history.push("/profile_settings");
            });
          } else {
            // toast.error(res.data);
          }
        })
        .catch((err) => toast.error(err.response.data));
    } else {
      toast.error("Alle Eingaben sind erforderlich.");
    }
  };

  const loginText = {
    header: "Ins Konto einloggen",
    helpText: "Sie haben kein Konto?",
    redirectText: "Sign Up",
    redirectPath: "/signup",
    handleClick: handleSignIn,
  };

  const signupText = {
    header: "Einen Account registrieren",
    helpText: "Sie haben bereits ein Konto? ",
    redirectText: "Login",
    redirectPath: "/login",
    handleClick: handleSignUp,
  };

  return (
    <Paper elevation={6} className={classes.paper} style={{ marginTop: "45px" }}>
      <Box display="flex" justifyContent="center" className={classes.loginHeader}>
        <img src={LogoImg} alt="logo_img" style={{ width: "50%" }} />
      </Box>
      {formType === "login" ? (
        <>
          <TextField
            name="email"
            label="E-mail"
            type="text"
            variant="outlined"
            margin="normal"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaRegEnvelope />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="password"
            label="Passwort"
            variant="outlined"
            margin="normal"
            type={visibility === true ? "text" : "password"}
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment style={{ padding: 0 }} position="start" component="div">
                  {visibility === true ? (
                    <div
                      style={{ cursor: "pointer" }}
                      onClick={(e) => {
                        setVisibility(false);
                      }}
                      className={classes.visiblediv}
                    >
                      <IconVisibilityOff style={{ padding: 0 }} />
                    </div>
                  ) : (
                    <div style={{ cursor: "pointer" }} onClick={(e) => setVisibility(true)}>
                      <IconVisiblility />
                    </div>
                  )}
                </InputAdornment>
              ),
            }}
          />
          {formType === "login" && (
            <Box display="flex" justifyContent="flex-end">
              <Link to="/forgotpwd">Vergessen?</Link>
            </Box>
          )}
        </>
      ) : (
        <>
          <TextField
            name="fullname"
            label="Name"
            type="text"
            variant="outlined"
            value={name}
            size="small"
            margin="normal"
            onChange={(e) => setName(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaUserAlt />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="email"
            label="E-mail"
            type="text"
            variant="outlined"
            value={email}
            size="small"
            margin="normal"
            onChange={(e) => setEmail(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaRegEnvelope />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="phone"
            label="Phone Number"
            type="number"
            variant="outlined"
            value={phone}
            size="small"
            margin="normal"
            onChange={(e) => setPhone(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaPhone />
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="password"
            label="Passwort"
            type={visibility === true ? "text" : "password"}
            variant="outlined"
            value={password}
            size="small"
            autoFocus
            margin="normal"
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment className={classes.adornment} position="start" component="div">
                  {visibility === true ? (
                    <div
                      style={{ cursor: "pointer" }}
                      onClick={(e) => {
                        setVisibility(false);
                      }}
                    >
                      <IconVisibilityOff />
                    </div>
                  ) : (
                    <div style={{ cursor: "pointer" }} onClick={(e) => setVisibility(true)}>
                      <IconVisiblility />
                    </div>
                  )}
                </InputAdornment>
              ),
            }}
          />
          <TextField
            name="confirm"
            label="Besttigen Passwort"
            type={visibility === true ? "text" : "password"}
            variant="outlined"
            value={confirm}
            autoFocus
            size="small"
            margin="normal"
            onChange={(e) => setConfirm(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FaLock />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment className={classes.adornment} position="start" component="div">
                  {visibility === true ? (
                    <div
                      style={{ cursor: "pointer" }}
                      onClick={(e) => {
                        setVisibility(false);
                      }}
                    >
                      <IconVisibilityOff />
                    </div>
                  ) : (
                    <div style={{ cursor: "pointer" }} onClick={(e) => setVisibility(true)}>
                      <IconVisiblility />
                    </div>
                  )}
                </InputAdornment>
              ),
            }}
          />
        </>
      )}

      <AuthButton buttonType={type1} click={isLoginForm ? loginText.handleClick : signupText.handleClick} />
      <Box style={{ marginTop: 10 }}>
        <Divider />
        <Box className={classes.helpText}>
          {isLoginForm ? loginText.helpText : signupText.helpText}
          <Link to={formType === "login" ? loginText.redirectPath : signupText.redirectPath} onClick={changeFormType}>
            {isLoginForm ? loginText.redirectText : signupText.redirectText}
          </Link>
        </Box>
      </Box>
    </Paper>
  );
};

Form.propTypes = {
  classes: PropTypes.object.isRequired,
  type1: PropTypes.string.isRequired,
};

export default withStyles(stylesForm)(Form);
