import React, { useState } from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";
import { useHistory, Link } from "react-router-dom";

import { withStyles } from "@mui/styles";
import { FaRegEnvelope } from "react-icons/fa";
import { Paper, Box, TextField, InputAdornment, Divider } from "@mui/material";

import stylesForm from "./stylesForm";
import LogoImg from "../../Pages/Assets/brandmark-design.png";

const ForgotForm = ({ classes }) => {
  const history = useHistory();
  const [email, setEmail] = useState();
  const sendEmailFunc = () => {
    if (!email) toast.error("Fgen Sie die E-Mail ein.");
    else history.push("/verify");
  };

  return (
    <Paper elevation={6} className={classes.paper} style={{ marginTop: "45px" }}>
      <Box display="flex" justifyContent="center" className={classes.loginHeader}>
        <img src={LogoImg} alt="logo_img" style={{ width: "50%" }} />
      </Box>

      <TextField
        name="email"
        label="E-mail"
        type="text"
        variant="outlined"
        margin="normal"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <FaRegEnvelope />
            </InputAdornment>
          ),
        }}
      />

      <div id="authButton" className="buttonContainer" onClick={sendEmailFunc}>
        <span>E-Mail Senden</span>
      </div>
      <Box style={{ marginTop: 10 }}>
        <Divider />
        <Box className={classes.helpText}>
          Sie haben kein Konto?
          <Link to="/login">Sign In</Link>
        </Box>
      </Box>
    </Paper>
  );
};

ForgotForm.propTypes = {
  classes: PropTypes.object.isRequired,
  type1: PropTypes.string.isRequired,
};

export default withStyles(stylesForm)(ForgotForm);
