import { Route, Switch } from "react-router-dom";
import React, { useState, useEffect } from "react";
import useStylesDashboard from "./stylesDashboard.js";
import { withTheme } from "@mui/styles";
import { Tabs, Tab, Button, Avatar } from "@mui/material";

import auth from "../../auth";
import Upgrade from "../Upgrade/Upgrade";
import Checkout from "../Checkout/Checkout";
import Event from "../../Components/Event/Event";
import Navbar from "../../Components/Navbar/Navbar";
import { getUserByIdFunc } from "../../actions/user.js";
import DashPanel from "../../Components/DashPanel/DashPanel";
import NewEventDialog from "../../Components/NewEventDialog/NewEventDialog";
import DisplayAppointments from "../../Components/DisplayAppointments/DisplayAppointments";
import NonAppointmentTable from "../../Components/NonAppointmentTable/NonAppointmentTable";

const Dashboard = () => {
  const classes = useStylesDashboard();
  const [user, setUser] = useState({ meetings: [] });
  //Tracks active tab
  const [value, setValue] = useState(0);
  //dialog state
  const [open, setOpen] = useState(false);
  const [editId, setEditId] = useState("");

  const [name, setName] = useState("");
  const [duration, setDuration] = useState(15);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleClickOpen = () => {
    setName("");
    setDuration(15);
    setEditId("");
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  //POSSIBLE TODO: find another way to render the new meeting in UI
  const renderNewMeeting = (meetings) => {
    const userClone = Object.assign({}, user);
    // userClone.meetings.push(meetings[meetings.length - 1]);
    userClone.meetings = meetings;
    // console.log(userClone, 'userclone');
    setUser(userClone);
  };

  useEffect(() => {
    const subId = auth.getSub();

    getUserByIdFunc(subId)
      .then((res) => {
        setUser(res.data);
      })
      .catch((e) => {
        console.log("Error: " + e);
      });
  }, []);

  return (
    <div className={classes.container}>
      <Navbar picture={user.picture ? user.picture : ""} name={user.fullName} />

      <Switch>
        <Route exact path="/dashboard/user/upgrade">
          <Upgrade user={user} setUser={setUser} />
        </Route>
        <Route exact path="/dashboard/user/upgrade/checkout">
          <Checkout user={user} setUser={setUser} />
        </Route>
        <Route path="/">
          <main className={classes.dashBody}>
            <div className={classes.subHeader}>
              <h1>Hallo Dr.{auth.getName()}</h1>
              <Tabs value={value} onChange={handleChange} indicatorColor="primary" textColor="primary">
                <Tab label="Event Types" />
                <Tab label="Scheduled Events" />
                {/* <Tab label="Appointment Type" /> */}
                <Tab label="Non Appointment" />
              </Tabs>
            </div>
            <DashPanel value={value} index={0}>
              <header className={classes.panelHeader}>
                <div className={classes.profileContainer}>
                  <Avatar src={user.picture} />
                  <div className={classes.profileInfo}>
                    <span>{user.fullName}</span>
                    {/* <span>http://dentoconnect.herokuapp.com/{user.url}</span> */}
                  </div>
                </div>
                {user.subscriber ? (
                  <React.Fragment>
                    <Button onClick={handleClickOpen} className={classes.addEventBtn} size="large" variant="outlined">
                      + New Event Type
                    </Button>
                  </React.Fragment>
                ) : null}
              </header>
              <section className={classes.events}>
                {user.meetings
                  .filter((meeting) => {
                    if (!user.subscriber) {
                      return meeting.duration === 60;
                    } else {
                      return true;
                    }
                  })
                  .map((meeting, index) => (
                    <Event
                      renderNewMeeting={renderNewMeeting}
                      url={user.url}
                      sub={user._id}
                      id={meeting._id}
                      user={`${user.given_name} ${user.family_name}`}
                      key={index}
                      name={name}
                      setEditId={setEditId}
                      setOpen={setOpen}
                      setDuration={setDuration}
                      duration={meeting.duration}
                      subscribe={meeting.subscribe}
                      meetingName={meeting.meetingName}
                    />
                  ))}
              </section>
              {/* <Button size="large" variant="contained" className={classes.getStartedBtn}>
                Getting Started Guide
              </Button> */}
            </DashPanel>
            {value === 1 && (
              <DashPanel value={value} index={1}>
                <section className={classes.events}>
                  <DisplayAppointments user={user._id} timezone={user.timezone} />
                </section>
              </DashPanel>
            )}
            {value === 2 && (
              <DashPanel value={value} index={2}>
                <NonAppointmentTable />
              </DashPanel>
            )}
          </main>
        </Route>
      </Switch>
      <NewEventDialog
        renderNewMeeting={renderNewMeeting}
        sub={user._id}
        open={open}
        name={name}
        id={editId}
        flag={name ? 1 : 0}
        setName={setName}
        duration={duration}
        setDuration={setDuration}
        handleClose={handleClose}
      />
    </div>
  );
};

export default withTheme(Dashboard);
