import React from "react";
import {
  Box,
  Table,
  TableRow,
  TableHead,
  TableCell,
  Tooltip,
  TableBody,
  IconButton,
  TableContainer,
  TablePagination,
} from "@mui/material";
import {
  MoreVert as IconMoreVert,
  PlayCircleOutline as IconPlayCircleOutline,
  Link as IconLink,
} from "@mui/icons-material";
import { getRecordFileFromDialogFlowServer } from "../../actions/dialogflow";
import moment from "moment";

const CustomTable = ({ tableDate, setRecordSrc, setOpenDialog }) => {
  const [page, setPage] = React.useState(0);

  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const getRecordFile = (cSid) => {
    getRecordFileFromDialogFlowServer(cSid).then((res) => {
      setRecordSrc(res);
      setOpenDialog(true);
    });
  };

  return (
    <>
      <TableContainer sx={{ maxHeight: 440 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell style={{ textAlign: "center" }} width={"15%"}>
                PATIENT
              </TableCell>
              <TableCell style={{ textAlign: "center" }} width={"30%"}>
                INHALT
              </TableCell>
              <TableCell style={{ textAlign: "center" }} width={"15%"}>
                AKTION
              </TableCell>
              <TableCell style={{ textAlign: "center" }} width={"15%"}>
                RECORDING
              </TableCell>
              <TableCell style={{ textAlign: "right" }} width={"15%"}>
                <Tooltip title="more">
                  <IconButton>
                    <IconMoreVert />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {tableDate &&
              tableDate.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => (
                <TableRow key={index}>
                  <TableCell style={{ textAlign: "center" }}>
                    <Box
                      display={"flex"}
                      flexDirection="column"
                      justifyContent={"center"}
                      style={{ textAlign: "center" }}
                    >
                      <h3 style={{ fontWeight: 700, fontSize: "16px" }}>{row.from}</h3>
                      <h4>{row.from}</h4>
                    </Box>
                  </TableCell>
                  <TableCell style={{ textAlign: "center" }}>{row.from}</TableCell>
                  <TableCell style={{ textAlign: "center" }}>
                    <Box display={"flex"} justifyContent="center" flexDirection={"column"}>
                      <Box textAlign="center" alignItems={"center"} display="flex" justifyContent={"center"}>
                        <p
                          style={{
                            backgroundColor: row.from ? "#ccffcc" : "#ffe0e0",
                            color: row.from ? "#118311" : "#ce8604",
                            borderRadius: "8px",
                            padding: "4px",
                          }}
                        >
                          {row.from ? " OK" : " Offten"}
                        </p>
                      </Box>
                      <Box>{moment(row.dateCreated).format("YYYY-MM-DD")}</Box>
                    </Box>
                  </TableCell>
                  <TableCell style={{ textAlign: "center" }}>
                    <Tooltip title="recording">
                      <IconButton onClick={() => getRecordFile(row.sid)}>
                        <IconPlayCircleOutline />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                  <TableCell>
                    <Box display={"flex"} justifyContent={"right"} alignItems="center">
                      <span style={{ marginRight: "12px" }}>{"PVS"}</span>
                      <IconLink></IconLink>
                      <Tooltip title="more">
                        <IconButton>
                          <IconMoreVert />
                        </IconButton>
                      </Tooltip>
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={tableDate.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
        labelRowsPerPage="Zeilen pro Seite"
      />
    </>
  );
};

export default CustomTable;
