import { Fragment, useState, useEffect } from "react";
import { Typography } from "@mui/material";
import PersonRoundedIcon from "@mui/icons-material/PersonRounded";
import { Scheduler } from "@aldabil/react-scheduler";
import Navbar from "../../Components/Navbar/Navbar";
import { makeResourceFunc, makeEventFunc } from "./data";
import { getUserByIdFunc } from "../../actions/user";
import { getAppointmentFuncForScheduler } from "../../actions/appointment";
import auth from "../../auth";

const SchedulerPage = () => {
  const [events, setEvents] = useState([]);
  const [resources, setResources] = useState([]);
  const [user, setUser] = useState({ meetings: [] });

  useEffect(() => {
    const subId = auth.getSub();
    getUserByIdFunc(subId)
      .then((res) => {
        setUser(res.data);
      })
      .catch((e) => {
        console.log("Error: " + e);
      });
  }, []);

  useEffect(() => {
    const fetchAppts = async () => {
      getAppointmentFuncForScheduler(user._id, user.timezone)
        .then((res) => {
          const newResources = makeResourceFunc(res.data);
          const newEvents = makeEventFunc(res.data);
          setEvents(newEvents);
          setResources(newResources);
        })
        .catch((e) => {
          console.error("Error: " + e);
        });
    };
    if (user._id) fetchAppts();
  }, [user]);

  const schedulerConfirm = (event, action) => {
    if (action === "edit") {
      return event;
    } else {
      return event;
    }
  };

  console.log(events);

  return (
    <Fragment>
      <Navbar picture={user.picture ? user.picture : ""} name={user.fullName} />
      {resources.length > 0 && events.length > 0 && (
        <Scheduler
          events={events}
          resources={resources}
          resourceFields={{
            idField: "_id",
            textField: "fullName",
            subTextField: "phone",
            // avatarField: "fullName",
            colorField: "color",
          }}
          onConfirm={schedulerConfirm}
          fields={[
            {
              name: "_id",
              type: "select",
              default: resources[0]._id,
              options: resources.map((res) => {
                return {
                  id: res._id,
                  text: `${res.fullName} (${res.phone})`,
                  value: res._id, //Should match "name" property
                };
              }),
              config: { label: "Assignee", required: true },
            },
          ]}
          viewerExtraComponent={(fields, events) => {
            return (
              <div>
                {fields.map((field, i) => {
                  if (field.name === "_id") {
                    const admin = field.options.find((fe) => fe.id === events._id);
                    return (
                      <Typography
                        key={i}
                        style={{ display: "flex", alignItems: "center" }}
                        color="textSecondary"
                        variant="caption"
                        noWrap
                      >
                        <PersonRoundedIcon /> {admin.text}
                      </Typography>
                    );
                  } else {
                    return "";
                  }
                })}
              </div>
            );
          }}
        />
      )}
    </Fragment>
  );
};

export default SchedulerPage;
