import React, { useRef } from "react";
import { withStyles } from "@mui/styles";
import { Tab, Tabs, Paper, InputBase, IconButton, Box } from "@mui/material";
import { Dialog, DialogContent, DialogContentText, DialogActions, Button } from "@mui/material";

import { Search as IconSearch } from "@mui/icons-material";

import NonAppointmentPannel from "./NonAppointmentPannel";
import CustomTable from "./CustomeTable";

import { getCallLogFromDialogFlowServer } from "../../actions/dialogflow";

const styles = (theme) => ({
  paper: {
    width: "100%",
    minHeight: "400px",
    marginTop: "50px",
    marginBottom: "100px",
  },
});

const NonAppointmentTable = ({ classes }) => {
  const soundRef = useRef();
  const [rows, setRows] = React.useState([]);
  const [recordSrc, setRecordSrc] = React.useState("");
  const [openDialog, setOpenDialog] = React.useState(false);

  React.useEffect(() => {
    getCallLogFromDialogFlowServer().then((res) => setRows(res));
  }, []);

  return (
    <>
      <Dialog fullWidth={true} maxWidth="sm" open={openDialog} onClose={() => setOpenDialog(false)}>
        <DialogContent>
          <DialogContentText>
            {recordSrc !== "" && <audio ref={soundRef} src={recordSrc} controls autoPlay />}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>Cancel</Button>
        </DialogActions>
      </Dialog>
      <Paper elevation={6} className={classes.paper}>
        <div>
          <Box display="flex" justifyContent={"center"}>
            <Box
              style={{
                margin: "16px",
                border: "solid 1px",
                borderRadius: "8px",
                width: "60%",
                borderColor: "#e2e2e2",
              }}
            >
              <IconButton type="button" sx={{ p: "10px" }} aria-label="search">
                <IconSearch />
              </IconButton>
              <InputBase
                sx={{ ml: 1, flex: 1 }}
                placeholder="Suchen nach Patient"
                inputProps={{ "aria-label": "search google maps" }}
              />
            </Box>
          </Box>
          <CustomTable tableDate={rows} setRecordSrc={setRecordSrc} setOpenDialog={setOpenDialog} />
        </div>
      </Paper>
    </>
  );
};

export default withStyles(styles)(NonAppointmentTable);
