import React, { useState } from "react";
import { toast } from "react-toastify";
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button, TextField } from "@mui/material";
import useStylesDialog from "./stylesDialog";
import "./newEventDialog.css";
import { makeSchedularEvent, updateSchedularEvent } from "../../actions/user";
import ConfirmDialog from "../Confirm/ConfirmDialog";

const NewEventDialog = ({ open, handleClose, sub, id, renderNewMeeting }) => {
  const classes = useStylesDialog();
  const [name, setName] = useState("");
  const [duration, setDuration] = useState(15);
  const [openDialog, setOpenDialog] = useState(false);

  const handleNameChange = (event) => {
    setName(event.target.value);
  };

  const handleSelectedClass = (event) => {
    const selected = document.getElementsByClassName("selected");

    //get selected element and remove selected class
    if (selected.length > 0) {
      selected[0].className = selected[0].className.replace("selected", "");
    }

    //add selected class to new element
    event.target.setAttribute("class", "selected");
    setDuration(event.target.value);
  };

  const handleSubmit = () => {
    const sendInfos = { meetingName: name, duration };
    makeSchedularEvent(sub, sendInfos)
      .then((res) => {
        if (res.status === 200) {
          renderNewMeeting(res.data.docs);
          handleClose();
        } else {
          handleClose();
          toast.error("Duplicated ScheduleEvent");
        }
      })
      .catch((e) => console.log(e));
  };
  const handleUpdate = () => {
    const sendInfos = { meetingName: name, duration };
    updateSchedularEvent(sub, sendInfos, id)
      .then((res) => {
        if (res.status === 200) {
          renderNewMeeting(res.data.docs);
          setOpenDialog(false);
          handleClose();
        } else {
          setOpenDialog(false);
          toast.error("Duplicated ScheduleEvent");
          handleClose();
        }
      })
      .catch((err) => {
        console.log(err.status);
        setOpenDialog(false);
        handleClose();
      });
  };

  return (
    <Dialog fullWidth={true} maxWidth="sm" open={open} onClose={handleClose}>
      <DialogTitle>{id ? "Edit Event" : "Create New Event"}</DialogTitle>
      <DialogContent>
        <DialogContentText>Enter a name for your event and select a duration.</DialogContentText>
        <TextField
          fullWidth
          required
          variant="outlined"
          margin="normal"
          label="Event name"
          value={name}
          onChange={handleNameChange}
        />
        <div className={classes.eventField}>
          <DialogContentText>Event Duration *</DialogContentText>
          <ul className={classes.listContainer}>
            <li className={`${duration === 15 ? "selected" : ""}`} value={15} onClick={handleSelectedClass}>
              15 min
            </li>
            <li className={`${duration === 30 ? "selected" : ""}`} value={30} onClick={handleSelectedClass}>
              30 min
            </li>
            <li className={`${duration === 45 ? "selected" : ""}`} value={45} onClick={handleSelectedClass}>
              45 min
            </li>
            <li className={`${duration === 60 ? "selected" : ""}`} value={60} onClick={handleSelectedClass}>
              60 min
            </li>
          </ul>
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>Cancel</Button>
        {id && name ? (
          <Button
            onClick={(e) => {
              setOpenDialog(true);
            }}
            disabled={name ? false : true}
          >
            Save
          </Button>
        ) : (
          <Button onClick={handleSubmit} disabled={name ? false : true}>
            Submit
          </Button>
        )}
      </DialogActions>
      <ConfirmDialog
        openDialog={openDialog}
        onOK={handleUpdate}
        onCancel={(e) => setOpenDialog(false)}
        title={"Confirm Dialog"}
        content={`Would you sure save this EventSchedule?`}
      />
    </Dialog>
  );
};

export default NewEventDialog;
