import React from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import { withStyles } from "@mui/styles";
import { ThemeProvider, CssBaseline } from "@mui/material";

import { theme } from "./themes/theme";
import Form from "./Components/Form/Form";
import ForgotForm from "./Components/Form/ForgotForm";
import VerifyCodeForm from "./Components/Form/VerifyCode";
import Confirm from "./Components/NewAppointment/Confirm";
import PrivateRoute from "./Components/PrivateRoute/PrivateRoute";

import OnBoarding from "./Pages/OnBoarding";
import SchedulePage from "./Pages/Scheduler";
import Profile from "./Pages/Profile/Profile";
import Calendar from "./Pages/Calendar/Calendar";
import Dashboard from "./Pages/Dashboard/Dashboard";
import ConfirmedAppointment from "../src/Pages/ConfirmedAppointment/ConfirmedAppointment";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";

const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_PUBLIC_KEY);

const stylesApp = (theme) => ({
  appLogo: {
    margin: "4rem 0 2rem 0",
  },
});

const App = ({ classes }) => {
  return (
    <Elements stripe={stripePromise}>
      <React.Fragment>
        <BrowserRouter>
          <ThemeProvider theme={theme}>
            <CssBaseline />
            <Switch>
              <Route path="/finish">
                <ConfirmedAppointment />
              </Route>
              <PrivateRoute path="/dashboard" component={Dashboard} />
              <PrivateRoute path="/scheduler" component={SchedulePage} />
              <Route exact path="/:url/:event/confirm" component={Confirm} />
              <Route exact path="/:url/:event/:eventDuration" component={Calendar} />
              <PrivateRoute path="/profile/:id" component={Profile} />
              <React.Fragment>
                <Switch>
                  <Redirect exact from="/" to="/login" />
                  <Route path="/login">
                    <Form type1="login" />
                  </Route>
                  <Route path="/signup">
                    <Form type1="signup" />
                  </Route>
                  <Route path="/forgotpwd">
                    <ForgotForm />
                  </Route>
                  <Route path="/verify">
                    <VerifyCodeForm />
                  </Route>
                  <PrivateRoute path="/profile_settings" type="profile" activeStep={0} component={OnBoarding} />
                  <PrivateRoute path="/confirm" type="confirm" activeStep={50} component={OnBoarding} />
                  <PrivateRoute path="/availability" type="availability" activeStep={100} component={OnBoarding} />
                </Switch>
              </React.Fragment>
            </Switch>
          </ThemeProvider>
        </BrowserRouter>
      </React.Fragment>
    </Elements>
  );
};

export default withStyles(stylesApp)(App);
