import React from "react";
import { useSelector } from "react-redux";
import { Box, Avatar, MenuItem, Popper, Grow, Paper, ClickAwayListener, MenuList } from "@mui/material";
import { Link, useHistory } from "react-router-dom";
import useStylesNavbar from "./stylesNavbar";
import LogoImg from "../../Pages/Assets/brandmark-design.png";

import auth from "../../auth";

function Navbar({ picture, name }) {
  const history = useHistory();
  const classes = useStylesNavbar();
  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef(null);
  const userId = useSelector((state) => state.auth.auth._id);
  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }

    setOpen(false);
    history.push(`/profile/${userId}`);
  };
  function handleListKeyDown(event) {
    if (event.key === "Tab") {
      event.preventDefault();
      setOpen(false);
    }
  }

  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = open;
  }, [open]);

  const handleLogOut = () => {
    auth.logout(() => {
      auth.sub = null;
      auth.email = null;
      auth.name = null;
      auth.token = null;
      history.push("/login");
    });
  };
  return (
    <header className={classes.navbar}>
      <Box display="flex" justifyContent="flex-start" className={classes.loginHeader}>
        <img src={LogoImg} alt="logo_img" style={{ width: "20%" }} />
      </Box>

      <div>
        <nav>
          <Link className={classes.link} to="/dashboard">
            Dashboard
          </Link>
          <Link className={classes.link} to="/scheduler">
            Calendar
          </Link>
          <Link className={classes.link} to="/dashboard/user/upgrade">
            Upgrade
          </Link>
        </nav>
        <div className={classes.profile}>
          <Avatar
            src={picture}
            ref={anchorRef}
            aria-controls={open ? "menu-list-grow" : undefined}
            aria-haspopup="true"
            onClick={handleToggle}
            style={{ cursor: "pointer" }}
          />
          <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                style={{ transformOrigin: placement === "bottom" ? "center top" : "center bottom" }}
              >
                <Paper>
                  <ClickAwayListener onClickAway={handleClose}>
                    <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                      <MenuItem onClick={handleClose} style={{ display: "flex", justifyContent: "space-between" }}>
                        Profile
                      </MenuItem>

                      <MenuItem
                        onClick={(e) => {
                          handleClose(e);
                          handleLogOut();
                        }}
                      >
                        Logout
                      </MenuItem>
                    </MenuList>
                  </ClickAwayListener>
                </Paper>
              </Grow>
            )}
          </Popper>
          <span>{name}</span>
        </div>
      </div>
    </header>
  );
}

export default Navbar;
