import moment from "moment";

export const makeResourceFunc = (data) => {
  const newData = [
    {
      _id: data[0].user,
      fullName: data[0].dentist.fullName,
      phone: data[0].dentist.phone,
      color: "#00BFB1",
    },
  ];
  return newData;
};

export const makeEventFunc = (data) => {
  const newEvents = data.map((item) => {
    const newData = {
      _id: item.user,
      event_id: item._id,
      title: `${item.guestName} - ${item.meetingName}`,
      start: new Date(moment(item.apptTime).tz(item.userTz)),
      end: new Date(moment(item.apptTime).tz(item.userTz).add(item.meetTime, "m")),
    };
    return newData;
  });
  return newEvents;
};
