import axios from 'axios';
import { get_call_log_url, get_recorde_file } from './constant';

export const getCallLogFromDialogFlowServer = () => {
  return axios.get(get_call_log_url).then((response) => {
    const callLogArray = response.data;
    return callLogArray;
  });
};

export const getRecordFileFromDialogFlowServer = (cSid) => {
  return axios.get(`${get_recorde_file}?sid=${cSid}`).then((response) => {
    const recordArray = response.data;
    return recordArray;
  });
};
