import React, { useEffect, useRef, useState } from "react";
import PropTypes from "prop-types";
import * as moment from "moment-timezone";
import { FiArrowLeftCircle } from "react-icons/fi";
import { useHistory, useLocation, useParams } from "react-router-dom";

import { withStyles } from "@mui/styles";
import {
  Paper,
  TextField,
  TextareaAutosize,
  Button,
  IconButton,
  Typography,
  MenuItem,
  FormControl,
  Select,
  FormHelperText,
} from "@mui/material";
import { ValidatorForm, TextValidator, SelectValidator } from "react-material-ui-form-validator";
import { FaRegCalendarCheck, FaGlobeAmericas, FaClock } from "react-icons/fa";
import stylesConfirm from "./stylesConfirm";
import { readAppointmentType, submitAppointmentFunc } from "../../actions/appointment";
import { getUsers } from "../../actions/user";
function Confirm({ classes }) {
  const ref = useRef();
  const weekdays = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];
  const location = useLocation();
  const { url } = useParams();
  const clientTz = location.state.clientTz;
  const appt = moment.tz(location.state.time, clientTz);
  const apptTime = appt.format(); //do not move down, moment is mutable

  const appointmentStr = `${appt.format("h:mma")} - ${appt.add(location.state.meeting.duration, "m").format("h:mma")}
  ${weekdays[appt.day()]}, ${months[appt.month()]} ${appt.date()}, ${appt.year()}`;

  const [nameField, setName] = useState("");
  const [emailField, setEmail] = useState("");
  const [comment, setComment] = useState("");
  const [dentist, setDentist] = React.useState("");
  const [eventType, setEventType] = React.useState();
  const [users, setUsers] = useState([{ _id: "", fullname: "" }]);
  const [envetTypes, setEventTypes] = useState([{ _id: "", title: "" }]);

  useEffect(() => {
    if (url === "demo") {
      alert("This is a demo account. Scheduling an event will not add it to any google accounts.");
    }
  }, [url]);

  useEffect(() => {
    getUsers()
      .then((res) => {
        setUsers(res.data.docs);
      })
      .catch((err) => {
        console.log(err);
      });
    readAppointmentType("a")
      .then((res) => {
        setEventTypes(res.data.docs);
      })
      .catch((err) => console.log(err));
  }, []);

  const handleName = (e) => {
    setName(e.target.value);
    // !e || !e.target.value
    //   ? setName("{ name: '', error: true, errorText: 'Name is required.' }")
    //   : setName({ name: e.target.value, error: false, errorText: '' });
  };

  const handleEmail = (e) => {
    // !e || !e.target.value
    //   ? setEmail({ email: '', error: true, errorText: 'Email is required.' })
    //   : setEmail({ email: e.target.value, error: false, errorText: '' });
    setEmail(e.target.value);
  };

  let history = useHistory();
  const handleBackButton = () => {
    history.push(`/${url}/${location.state.meeting._id}/${location.state.meeting.duration}`); //back to scheduler
  };

  const submitForm = () => {
    // console.log('------------------------');
    // if (!nameField.name || !emailField.email) {
    //   handleName();
    //   handleEmail();
    // }
    // if (url === 'demo') {
    //   history.push('/finish', {
    //     url: url,
    //     name: location.state.name,
    //     meeting: location.state.meeting.meetingName.title,
    //     apptTime: appointmentStr,
    //     timezone: location.state.clientTz,
    //   });
    //   return;
    // }

    const appointmentInfo = {
      guestName: nameField,
      guestEmail: emailField,
      guestComment: comment,
      guestTz: clientTz,
      meetingName: location.state.meeting.meetingName,
      meetTime: location.state.meeting.duration,
      apptTime: apptTime,
      url,
      dentist: dentist,
      eventSchedule: eventType,
    };

    submitAppointmentFunc(appointmentInfo).then((res) => {
      if (res.status !== 201) {
        history.push(`/${url}/${location.state.meeting.duration}`); //go back to scheduler, error alert?
      } else {
        history.push("/finish", {
          url: url,
          name: location.state.name,
          meetingName: location.state.meeting.meetingName,
          apptTime: appointmentStr,
          timezone: location.state.clientTz,
        });
      }
    });
  };

  return (
    <Paper elevation={6} className={classes.paper}>
      <div className={classes.meetingInfo}>
        <IconButton className={classes.backButton} aria-label="back-to-scheduler" onClick={handleBackButton}>
          <FiArrowLeftCircle size={45} />
        </IconButton>
        <Typography variant="h6" className={classes.name}>
          {location.state.name}
        </Typography>
        <Typography variant="h4" className={classes.meetingName}>
          {location.state.meeting.meetingName}
        </Typography>

        <div className={classes.eventDetails}>
          <div className={classes.meetingDuration}>
            <FaClock size={23} />
            <span>{location.state.meeting.duration} min</span>
          </div>
          <div className={classes.eventTime}>
            <FaRegCalendarCheck size={23} /> <span>{appointmentStr}</span>
          </div>
          <div className={classes.timeZone}>
            <FaGlobeAmericas size={23} />
            <span>{clientTz.replace("_", " ") + " (GMT" + moment.tz(clientTz).format("Z") + ")"}</span>
          </div>
        </div>
      </div>

      <div className={classes.confirmForm}>
        <h2>Enter Details</h2>
        <ValidatorForm className={classes.form} onSubmit={submitForm}>
          <div style={{ marginTop: "0.8rem", marginBottom: "0.2rem" }}>Dentist:</div>
          <SelectValidator
            value={dentist}
            name={"dentist"}
            variant="outlined"
            fullWidth
            onChange={(e) => setDentist(e.target.value)}
            validators={["required"]}
            errorMessages={["Dentist field is required"]}
          >
            {users.map((user, index) => (
              <MenuItem value={user._id} key={index}>
                {user.fullName}
              </MenuItem>
            ))}
          </SelectValidator>

          <div style={{ marginTop: "0.8rem", marginBottom: "0.2rem" }}>Name:</div>
          <TextValidator
            className={classes.textField}
            id="name-field"
            variant="outlined"
            name="nameField"
            value={nameField}
            validators={["required"]}
            errorMessages={["Name field is required"]}
            onChange={handleName}
          />
          <div style={{ marginTop: "0.8rem", marginBottom: "0.2rem" }}>Email: </div>
          <TextValidator
            className={classes.textField}
            variant="outlined"
            onChange={handleEmail}
            name="email"
            value={emailField}
            validators={["required", "isEmail"]}
            errorMessages={["Email field is required", "email is not valid"]}
          />

          <div style={{ marginTop: "0.8rem", marginBottom: "0.2rem" }}>Meeting Notes: </div>
          <TextareaAutosize
            className={classes.textArea}
            aria-label="meeting-comments"
            onChange={(e) => setComment(e.target.value)}
          />
          <div style={{ marginTop: "1rem", marginBottom: "0.2rem" }}></div>
          <Button className={classes.button} variant="contained" type="submit" color="primary">
            Schedule Event
          </Button>
        </ValidatorForm>
      </div>
    </Paper>
  );
}

Confirm.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(stylesConfirm)(Confirm);
